@use 'base/variables' as v;
@use 'base/mixins' as m;


.carreras{
    background-color: v.$azuloscuro;
    padding-bottom: 4rem;
    padding-top: 6rem;
    box-shadow: 0 0 2rem rgba(10, 29, 85, 0.8);
    h4{ text-align: center;
        color: v.$blanco;
        margin-bottom: 5rem;
        span{
            font-size: 3rem;
            display: none;
        }
        .maquina-escribir{   
            display:inline-flex;
            white-space: nowrap;
            border-right: 4px solid v.$blanco;
            width: 32.5rem;
            animation: escribir 4s steps(18), blinks .5s infinite step-end alternate;
            overflow: hidden;
        
        @keyframes escribir {
            from{ width: 0;}
        }
        @keyframes blinks {
            50% {border-color: transparent;}
        }}
        
    }
}

.slider-container{
    padding: 1rem 0;
    .tarjetas{
        display: flex;
        align-items: center;
        width: 100%;
        //height: 40rem;
        margin: auto;
        overflow-x: hidden;
        &::-webkit-scrollbar{
            display: none;
        }
        .tarjeta{ // = tarjeta
            box-shadow: 3px 3px 11px rgba(207, 36, 36, 0.2);
            //min-width: 20rem;  //si el navegador calcula que el elemento es inferior a 20rem esta propedad hace que tome los 20rem
            min-width: 100%;
            margin-right: 1.2rem;
            position: relative;
            border: 3rem solid v.$azuloscuro;

            @media (min-width: 480px){  //me dispone de 2 tarjetas
                min-width: 50%;
                border: none;
            }
            @media (min-width: 768px){  //me visualiza 3 tarjtas
             min-width: 33%;
            }
            @media (min-width: 960px){  // me muestra 5 tarjetas
                min-width: 19.13%;
            }
            .imagen{
                width: 100%;
                height: 40rem;
                img{
                    width: 100%;
                    height: 100%;
                    border-radius: .8rem;
                }
            }
            .descrpcion-slider{
                width: 100%;
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                text-align: center;
                .slider_cursos{
                    color: v.$grisclaro;
                    font-weight: 500;
                    background-color: rgba(v.$gris, .2);
                    padding: 1rem 2rem;
                    border-radius: .8rem;
                    border: 1px solid rgba(v.$gris, .4);
                    &:hover{
                        background-color: rgba(v.$gris, .4);
                        color: v.$blanco;
                        border: 1px solid rgba(v.$gris, .8);
                    }
                }
                h4{
                    margin-top: 3rem;
                    font-size: 2rem;
                    .Ningles{
                        font-size: 2rem;
                        display: inline;
                        color: v.$azul;
                        font-weight: 900;
                    }
                }
            }   
        } //fin tarjeta
    } //fin tarjetas
    .flechas-atras-adelante{
        margin-top: 3rem;
        display: flex;
        justify-content: space-around;
        @media (min-width: 480px) {
            justify-content: center;
            gap: 7rem;
        }
        img{
            width: 4rem;
            &:hover{
                cursor: pointer;
            }
        }
    }

} //fin slider-container


.tarjeta{
    overflow: hidden;  // cunado se le da zoom corte la imagen q sobre salga del div.tarjeta
    &:hover{
        cursor: pointer;
        img{
            transition: transform 1s ease;
            transform: scale(1.1);
        }
    }

    transition: right 1500ms ease;
    right: -0rem;   //se mueve en 28
}

