@use 'base/variables' as v;
@use 'base/mixins' as m;

footer{
    background-color: v.$negrofooter;
    padding: 8rem 0 3rem 0;
    h3{
        margin-bottom: 5rem;
        text-align: center;
        color: v.$blanco;
        text-transform: uppercase;
    }

    .footer-contenido{
        border-top: 1px solid v.$grisoscuro;
        p{ color: v.$grisoscuro;}

        @include m.tablet{
            display: grid;
            grid-template-columns: repeat(3, 1fr);
        }

        .footer-logo{
            margin-top: 3rem;
            padding: 0 4rem 0 4rem;
            text-align: center;
            img{
                width: 12rem;
            }
        }

        .footer-menu{    
            margin-top: 3rem;
            h3{margin-bottom: 2rem;}
            a{
              font-size: 1.5rem;
              font-weight: 500;
              display: block;
              color: v.$grisoscuro;
              text-align: center;
              margin-bottom: 1.2rem;
              transition: all 0.5s ease;
                &:hover{
                    color: v.$blanco;
                }
            }
        }
        
        .footer-contacto{    
            margin-top: 3rem;
            h3{margin-bottom: 2rem;}
            img{width: 2.7rem;}
            .footer-container{
                position: relative;
                height: 10rem;
                .subcantactos{
                    width: 26rem;
                    position: absolute;
                    top: 0;
                    left: 50%;
                    transform: translateX(-50%);
                    @media (min-width: 360px){
                        width: 32rem;
                    }
                    @media (min-width: 768px){
                        width: 26rem;
                    }
                   
                    .subcantacto{
                        display: flex;
                        align-items: center;
                        justify-content: left;
                        @media (min-width: 480px){
                            justify-content: center;
                        }
                        @media (min-width: 768px){
                            justify-content: left;
                        }
                        margin-bottom: 1.2rem;
                    }  
                }
            }      
        } // fin footer contacto
    } //fin footer contenido

    .all-right{
        text-align: center;
        margin-top: 4rem;
        border-top: 1px solid v.$grisoscuro;
        p{
            margin-top: 2rem;
            color: v.$grisoscuro;
            a{color: v.$azul; }
        }
    }
} // fin footer