@use 'base/variables' as v;
@use 'base/mixins' as m;

.cabezote{  //encierra las 2 barras superiores y el slider con bkg de fondo
    .bkg{min-height: 0rem;}
    .container{
        min-height: 70rem;  //altura que le da a la imagen de fondo
        padding-top: 0rem;
        position: relative;
       // transition: background-Image 1s ease-in-out;
        background-position: center center;
        background-size: cover;
    }
    .info{  //barra de rrss
        padding: 0 4rem;
        background-color: v.$blanco;
        img{  
            width: 2.0rem; 
            max-width: none;
        }
        @include m.tablet{
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
        .info-basica{ 
            .logo-cm{width: 4rem;}
            list-style: none;
            padding: 0;
            margin: 0;   
            @include m.telefono{
               display: grid;
               grid-template-columns: 50% 50%;
            }
            @include m.tablet{
                display: flex;
                align-items: center;
                gap: 1.3rem;
            }
            li{
                &:nth-child(3){
                    grid-column: 1/3;
                }
                &:nth-child(4){
                    grid-column: 1/3;
                }
                display: flex;
                align-items: center;
                justify-content: center;
                gap: .5rem;
                font-size: 1.4rem;     
            }
        }
        .rrss{
            margin-top: 1rem;
            @include m.tablet{
                margin-top: 0rem;
            }
            text-align: center;
            a{ margin-right: 1rem;}
        }
    }

    .barrafija{  //clase q se agrega en slider.js
        position: fixed;
        width: 100%;
        top: 0;
        z-index: 1;
    }
    .barra-header{  //barra superior
    
        background-color: rgba(10, 29, 85, 1);
        /*
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        align-items: center;
        */
        display: grid;
        grid-template-columns: 50% 50%;
        padding: 0 2rem 0 1.5rem;
        @include m.tablet {   // cuando supera esta resolucion los organiza horizontalmente el logo y el nav
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
        }

        .column-logo{    
            img{  //logo principal
            width: 12rem;
            transition: width 0.4s ease-in-out;
            }
        }

        .movil-menu {
            margin-top: 3rem;
            text-align: right;
            @include m.tablet {  //cunado es > 768px oculta el div que contiene la img(barra menu)
              display: none;
            }
            img {  width: 4rem; }
            &:hover{ cursor: pointer;}
        }

        .column-nav{    
            grid-column: 1/3; 
            nav{
                height: 0;
                visibility: hidden;
                opacity: 0;
                //display: none;
                transition: height 1.3s ease, visibility 0.3s ease-in-out, opacity 0.2s linear;
                   
                @include m.tablet {  //cunado llegue a esta resolucion toma los <a></a> y los convierte a flex
                    visibility: visible; 
                    display: flex;
                    align-items: center;
                    opacity: 1; 
                    //height: auto;
                }
                a{
                    display: block;
                    text-align: center;
                    color: v.$gris;
                    margin-bottom: 1rem;
                    text-transform: uppercase;
                    padding-bottom: 1.2rem;
                    font-size: 1.5rem;
                    font-weight: 500;
                    font-family: v.$fuente_secundaria;
                    position: relative;
                    @include m.tablet { 
                        margin-bottom: 0;
                        margin-top: 1rem;
                        font-size: 1.4rem;
                        margin-right: 1.2rem;
                     }
                    @include m.tabletl { 
                        font-size: 1.5rem;
                     }
                    //margin-bottom: 1.2rem;
                    //background-color: rgb(99, 182, 154);
                    &:last-of-type{ //se selecciona el ultimo href
                        @include m.tablet{
                        margin-right: 2rem;}
                    }
                    transition: all 0.5s ease;
                    &::after{
                        content: "";
                        position: absolute;
                        bottom: 0;
                        left: 50%;
                        width: 0%;
                        height: 2px;
                        background-color: v.$gris;
                        transition: all 0.5s ease;
                    }
                    &:hover{
                        color: v.$blanco;
                    }
                    &:hover::after{
                        width: 100%;
                        left: 0;
                    }
                }  
            } 

            .mostrar_nav { //clase que se agrega con js en la <nav>....<nav> cuando se da click en el btn menu
                //display: block; // muestra la nav.../nav
               visibility: visible;
                opacity: 1;
                height: 24rem;
                transition: opacity 2.2s ease, height 0.3s ease-in, visibility 2.2s ease-in; 
                @include m.tablet{ height: 0;}   
            }    
        }//fin columna nav
    }  //fin barra superior



    .bkg-flechas-sliders{  //slider del bkg
        position: absolute;  
        top: 80%;   // en dispositivo peque√±o me baha las flechas del slider background a un 80%
        left: 0%;
        right: 0%;
        @include m.tablet{  //en dispositivo mediano lo pone aprox a la mitad
            top: 49%;
        }
       
        .sliders-bkg{
            display: flex;
            align-items: flex-start;
            justify-content: space-between;
            overflow: hidden;
            
            img{
                width: 6rem;
                border-radius: 50%;
                padding: .5rem;
                margin: 3.7rem auto;
                @include m.telefono { 
                    margin: 2rem auto;
                 }
                @include m.tablet { 
                    margin: 6rem auto;
                 }
                @include m.tabletl { 
                    margin: 8rem auto;
                 }
                //background-color: rgba(199, 199, 199, 0.267);
                }
            img:hover{ cursor: pointer; background-color: rgba(2, 33, 60, 0.7);}
    
            h2{  //tiene clase eslogan el cual se seleccona con js
                color: v.$blanco;
                font-family: v.$fuente_secundaria;
                font-weight: 600;
                visibility: hidden;
                transform: scale(1.5);
                transition: visibility 400ms;
                transition: transform 500ms;
                font-size: 2.4rem;
                @include m.telefono { 
                    font-size: 2.9rem;
                 }
                @include m.tablet { 
                    font-size: 5rem;
                 }
                 @include m.tabletl { 
                    font-size: 5.2rem;
                  }
                @include m.desktop { 
                    font-size: 6.6rem;
                 }
                span{
                    padding: 0rem 1.3rem;
                    text-transform: uppercase;
                    background-color: rgb(211, 248, 0);  //  =  #D3F800) //#BBFF13 //#8EE700  //09EA00
                    color: rgb(0, 0, 0);  //color: rgb(0, 102, 255); color: rgb(0, 139, 139);
                }
            }
        }
    }
}  //fin container

.cabezote{
    position: relative;
}
    

    //////// secciones del header contiene los 3 rectangulos blancos con iconos verdes  /////////
    .contenido-secciones{
        @include m.tablet{
        position: absolute;
        left: 0;
        right: 0;
        bottom: -9%;       }

        .secciones-header{

            @include m.tablet{
            display: flex;
            justify-content: center;}

            p{
                font-weight: 700;
                color: v.$grisoscuro;
                margin-top: 1rem;
                position: relative;
                transition: all 1.1s;
            }
            a{
                font-weight: 700;
                font-size: 1.4rem;
                color: v.$grisoscuro;
                margin-top: 1rem;
                position: relative;
                transition: all 1.1s;
            }
            h4{
                color: v.$azuloscuro;
                position: relative;
                transition: all 1.1s;
            }

            .seccion-header{
                display: grid;
                grid-template-columns: 1fr 3fr;
                align-items: center;
                margin: 0 6rem;
                gap: 2rem;
                position: relative; 
                background-color: v.$blanco;
                text-align: center;
                padding: 1rem;
                // @media (min-width: 480px) {
                // display: flex;
                // justify-content: space-around;
                // align-items: center;
                // }
                @include m.tablet { 
                    width: 35rem;
                    height: 15rem; 
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    gap: 1.5rem; 
                    margin: 0;
                    margin-right: .4rem;
                    border-radius: .8rem;
                 }
                
                img{  //imagenes svg verdes de los 3 cuadros inferiore dle header
                width: 9rem;
                position: relative;
                }
        
                &:hover{
                    cursor: pointer;
                    p, h4 {
                        color: v.$azul;
                        @include m.tablet { 
                            color: v.$blanco;
                         }
                    }
                }                 
            }
            @include m.tablet { 
                .seccion-header::before{
                    content: '';
                    width: 0;
                    height: 0;
                    background-color: v.$azul2;
                    position: absolute;
                    bottom: 0;
                    left: 0; 
                    transition: all 0.6s ease;
                    border-radius: .8rem; 
                }
                .seccion-header:hover::before{ //hover al seudo elemento
                    cursor: pointer;
                    width: 100%;
                    height: 100%;
                }
             }
            
            
        }
    }
