@use 'base/variables' as v;
@use 'base/mixins' as m;


.cabezote {
    .nosotros {
        min-height: 65rem;  //altura que le da a la imagen de fondo
        padding-top: 0rem;
        position: relative;
        transition: background-Image 1s ease-in-out;
        background-position: center center;
        background-size: cover;
        @include m.telefono { 
            min-height: 62rem;
        }
        @include m.tablet { 
            min-height: 50rem;
        }
        @include m.tabletl { 
            min-height: 53rem;
        }
       /* @include m.desktop { 
            min-height: 75rem;
        }
        @include m.desktopxl { 
            min-height: 76rem;
        }*/
    }
}

.header {
    padding: 5rem 0;
    position: relative;
    min-height: 40rem;
    @include m.desktop { 
      //  min-height: 60rem;
    }

    &::before {
        content: '';
        background-color: v.$negro;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        opacity: .5;
    }
}

.header-nosotros {
    background-image: url(../img/nosotros.jpg); 
    filter: drop-shadow( 5px 5px 10px #e1e1e1 );
    position: absolute; 
    width: 100%;
    background-size: cover;
    background-position: center center;
    
    .nosotros-header {
        h2 {
            font-size: 3rem;
            font-family: v.$fuente_principal;
            font-weight: 600;
            position: absolute;
            left: 3rem;
            bottom: 0;
            color: v.$blanco;
            text-transform: uppercase;
            &::after {  //creacion de seudo elemento, pinta linea horizontal verde antes del h2 ="cursos-populares" 
                content: ''; 
                width: 19rem;
                height: 0.5rem;
                display: block;
                background-color: v.$azul;
                margin: auto;
                margin-bottom: 3rem;
                @include m.tablet { 
                    width: 35rem;
                 }
                @include m.tabletl { 
                    width: 24rem;
                 }
                @include m.desktop { 
                    width: 33rem;
                 }
            }
            @include m.tablet { 
                font-size: 4rem;
            }
            @include m.desktop { 
                font-size: 6rem;
            }
        }
    }
}

.contenido-principal {
    
    @include m.telefono { 
        padding-top: -3rem;
     }
    @include m.tablet { 
        padding-top: 8rem;
     }
     @include m.tabletl { 
        padding-top: 2rem;
      }
    @include m.desktop { 
        padding-top: 0rem;
     }
}

.imagen-estudiante{
    @include m.desktop{
        height: 48rem;
    }
   img{
       height: 100%;
       width: 100%;
   }
}

.texto-bienvenida {
    font-size: 4.3rem;
    padding-top: 2.8rem;
    font-family: v.$fuente_principal;
    font-weight: 700;
    color: v.$azuloscuro;
    margin-bottom: 1rem;
    span {
        display: block;
        color: v.$grisoscuro;
        font-size: 4.8rem;
    }
}

.bloque-historia {
    display: flex;
    flex-direction: column;
    justify-content: center;
    .title-lineal {
        display: block;
        margin: 0 auto;
        padding-bottom: 2rem;
        transition:all .5s ease-in-out;
        &:hover {
            -webkit-transform: rotateY(180deg);
            -webkit-transform-style: preserve-3d;
            transform: rotateY(180deg);
            transform-style: preserve-3d;
        }
    }
}
.bloque-texto {
    @include m.tablet { 
        padding: 0 15rem;
     }
    
    p {
        font-family: v.$fuente_secundaria;
        font-size: 1.8rem;
        padding: 0 1.2rem;
        line-height: 1.9;
        text-align: justify;
        margin-bottom: 2.5rem;
        white-space: pre-line;
    }
}

.acceso-rapido {
    padding: 2rem;
    .acceso-texto {
        text-align: center;
        margin-bottom: 3rem;
    }
    .bloque-acceso-rapido {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 2rem;
        
        @include m.tablet { 
            display: flex;
            justify-content: space-between;
            margin-top: 5rem;
         }
         
         @include m.tablet { 
            a {
                width: 100%;
            }
          }
        
        .contenido-bloque-acceso {
            display: flex;
            flex-direction: column;
            justify-content: space-evenly;
            background-color: v.$grisoscuro;
            // animation: ;
            animation-name: cambiarColor;
            animation-duration: 3s;
            animation-iteration-count: infinite;
            &:hover {
                animation-play-state: paused;
            }
        }

        @keyframes cambiarColor {
            from {
                background-color: v.$azul2;
            }
            to {
                background-color: v.$color1;
            }
        }
        .contenido-bloque-acceso1 {
            display: flex;
            flex-direction: column;
            justify-content: space-evenly;
            background-color: v.$color1;
            // animation: ;
            animation-name: cambiarColor1;
            animation-duration: 3s;
            animation-iteration-count: infinite;
            &:hover {
                animation-play-state: paused;
            }
        }
        @keyframes cambiarColor1 {
            from {
                background-color: v.$grisoscuro;
            }
            to {
                background-color: v.$azul2;
            }
        }
    }
}

.bloque-acceso-rapido {
    .contenido-enlace {
        &:nth-child(3) {
            grid-column: 2 / 3;
            grid-row: 2 / 3;
        }
        &:nth-child(4) {
            grid-column: 1 / 2;
            grid-row: 2 / 3;
        }
    }
}

.contenido-bloque {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 2rem;
    height: 15rem;
    @include m.tablet { 
        
     }
    p {
        color: v.$blanco;
        font-size: 2rem;
        @include m.tablet { 
            font-size: 2rem;
         }
    }
}
