@use 'base/variables' as v;
@use 'base/mixins' as m;


.banda1{ //banda de color azul oscuro despues del header.. dentro de este div esta el seudoelemento
    .line-seudoelement{
        @include m.tablet{
        margin-top: 6rem;  // al a linea verde seudoelemento que es en componentes.scss se a√±ade margin superior de 7rem +
        margin-bottom: 2rem;
    }   }
    background-color: v.$azuloscuro;
    padding: 4rem 0rem 3rem 0rem;
    
    h3, p {
        font-family: v.$fuente_principal;
        line-height: 1.8;
        color: v.$blanco;
        text-align: center;
        margin-bottom: 0;}
    span{
        color: v.$azul;
        font-size: 1.8rem;
        font-weight: 600;
    }
    border-bottom: 1px solid v.$gris;
}

//////////////////////////////banda de los contadores numericos/////////////////////////////

.banda2{
    background-color: v.$azuloscuro;
    padding: 3rem 0;
    .modo-banda2{
         
        background-image: radial-gradient(v.$blanco 10%, transparent 0);
        background-size: 10px 10px;
        padding: 3rem 2rem;

        @include m.telefono{
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            column-gap: 2rem;
            row-gap: 2rem;
        }
        @media (min-width: 960px){
            display: grid;
            grid-template-columns: repeat(5, 1fr);
            column-gap: 2rem;
        }

        .banda2-campo{
            background-color: v.$blanco;
            border-radius: .5rem;
            img{
                width: 12rem;  
            }
            @media (max-width: 959px){  //hasta 959px me ejecuta el codigo
            &:nth-of-type(5){
                grid-column: 1/3;}
            }  
            margin-bottom: 1rem;
            margin-top:  1rem;
            text-align: center;
            display: flex;
            flex-direction: column;
            align-items: center;
            color: v.$azuloscuro; //#7c7c7c;
            padding: 2rem 0;
            .num{
                font-size: 3.5rem;
                font-weight: 700;
            }
            .text{
                font-size: 2rem;
            }
        }
    }
}

/////////////////convenios de certificacion ////////////////////
.convenios{
    .text{
        background-color: v.$azuloscuro;
        padding: 3rem 1rem 2rem 1rem;
        h2{
            color: v.$blanco;
            margin: 0;
            text-transform: uppercase;
        }
        p{
            color: v.$azul;
            text-align: center;
            font-size: 1.8rem;
            text-transform: uppercase;
        }
    }
    .contenido-convenios{
        padding: 1rem 0;

        /*display: flex;
        justify-content: space-between;*/
        @include m.grid(2, 1rem);
        @include m.telefono{
            @include m.grid(3, 1rem);
        }
        @include m.tabletl{
            @include m.grid(4, 1rem);
        }
        @include m.desktop{
            @include m.grid(8, 1rem);
        }
        
        .convenio{
            text-align: center;
            img{
                width: 12rem;
            }
        }
        .tomedent{
           img{
               width: 20rem;
           }
        }
        .ansa{
            img{
                height: 13rem;
            }
        }
    }
}
