h2 {
  font-size: 2.8rem;
  font-weight: 700;
}

h3 {
  font-size: 2rem;
}

h4 {
  color: #FFFFFF;
  font-size: 1.7rem;
  margin: 0;
}

p {
  font-size: 1.4rem;
  margin: 0;
}

.cabezote .bkg {
  min-height: 0rem;
}
.cabezote .container {
  min-height: 70rem;
  padding-top: 0rem;
  position: relative;
  background-position: center center;
  background-size: cover;
}
.cabezote .info {
  padding: 0 4rem;
  background-color: #FFFFFF;
}
.cabezote .info img {
  width: 2rem;
  max-width: none;
}
@media (min-width: 768px) {
  .cabezote .info {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
.cabezote .info .info-basica {
  list-style: none;
  padding: 0;
  margin: 0;
}
.cabezote .info .info-basica .logo-cm {
  width: 4rem;
}
@media (min-width: 480px) {
  .cabezote .info .info-basica {
    display: grid;
    grid-template-columns: 50% 50%;
  }
}
@media (min-width: 768px) {
  .cabezote .info .info-basica {
    display: flex;
    align-items: center;
    gap: 1.3rem;
  }
}
.cabezote .info .info-basica li {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  font-size: 1.4rem;
}
.cabezote .info .info-basica li:nth-child(3) {
  grid-column: 1/3;
}
.cabezote .info .info-basica li:nth-child(4) {
  grid-column: 1/3;
}
.cabezote .info .rrss {
  margin-top: 1rem;
  text-align: center;
}
@media (min-width: 768px) {
  .cabezote .info .rrss {
    margin-top: 0rem;
  }
}
.cabezote .info .rrss a {
  margin-right: 1rem;
}
.cabezote .barrafija {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 1;
}
.cabezote .barra-header {
  background-color: rgb(10, 29, 85);
  /*
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
  */
  display: grid;
  grid-template-columns: 50% 50%;
  padding: 0 2rem 0 1.5rem;
}
@media (min-width: 768px) {
  .cabezote .barra-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.cabezote .barra-header .column-logo img {
  width: 12rem;
  transition: width 0.4s ease-in-out;
}
.cabezote .barra-header .movil-menu {
  margin-top: 3rem;
  text-align: right;
}
@media (min-width: 768px) {
  .cabezote .barra-header .movil-menu {
    display: none;
  }
}
.cabezote .barra-header .movil-menu img {
  width: 4rem;
}
.cabezote .barra-header .movil-menu:hover {
  cursor: pointer;
}
.cabezote .barra-header .column-nav {
  grid-column: 1/3;
}
.cabezote .barra-header .column-nav nav {
  height: 0;
  visibility: hidden;
  opacity: 0;
  transition: height 1.3s ease, visibility 0.3s ease-in-out, opacity 0.2s linear;
}
@media (min-width: 768px) {
  .cabezote .barra-header .column-nav nav {
    visibility: visible;
    display: flex;
    align-items: center;
    opacity: 1;
  }
}
.cabezote .barra-header .column-nav nav a {
  display: block;
  text-align: center;
  color: #e1e1e1;
  margin-bottom: 1rem;
  text-transform: uppercase;
  padding-bottom: 1.2rem;
  font-size: 1.5rem;
  font-weight: 500;
  font-family: "Varela Round", sans-serif;
  position: relative;
  transition: all 0.5s ease;
}
@media (min-width: 768px) {
  .cabezote .barra-header .column-nav nav a {
    margin-bottom: 0;
    margin-top: 1rem;
    font-size: 1.4rem;
    margin-right: 1.2rem;
  }
}
@media (min-width: 820px) {
  .cabezote .barra-header .column-nav nav a {
    font-size: 1.5rem;
  }
}
@media (min-width: 768px) {
  .cabezote .barra-header .column-nav nav a:last-of-type {
    margin-right: 2rem;
  }
}
.cabezote .barra-header .column-nav nav a::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0%;
  height: 2px;
  background-color: #e1e1e1;
  transition: all 0.5s ease;
}
.cabezote .barra-header .column-nav nav a:hover {
  color: #FFFFFF;
}
.cabezote .barra-header .column-nav nav a:hover::after {
  width: 100%;
  left: 0;
}
.cabezote .barra-header .column-nav .mostrar_nav {
  visibility: visible;
  opacity: 1;
  height: 24rem;
  transition: opacity 2.2s ease, height 0.3s ease-in, visibility 2.2s ease-in;
}
@media (min-width: 768px) {
  .cabezote .barra-header .column-nav .mostrar_nav {
    height: 0;
  }
}
.cabezote .bkg-flechas-sliders {
  position: absolute;
  top: 80%;
  left: 0%;
  right: 0%;
}
@media (min-width: 768px) {
  .cabezote .bkg-flechas-sliders {
    top: 49%;
  }
}
.cabezote .bkg-flechas-sliders .sliders-bkg {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  overflow: hidden;
}
.cabezote .bkg-flechas-sliders .sliders-bkg img {
  width: 6rem;
  border-radius: 50%;
  padding: 0.5rem;
  margin: 3.7rem auto;
}
@media (min-width: 480px) {
  .cabezote .bkg-flechas-sliders .sliders-bkg img {
    margin: 2rem auto;
  }
}
@media (min-width: 768px) {
  .cabezote .bkg-flechas-sliders .sliders-bkg img {
    margin: 6rem auto;
  }
}
@media (min-width: 820px) {
  .cabezote .bkg-flechas-sliders .sliders-bkg img {
    margin: 8rem auto;
  }
}
.cabezote .bkg-flechas-sliders .sliders-bkg img:hover {
  cursor: pointer;
  background-color: rgba(2, 33, 60, 0.7);
}
.cabezote .bkg-flechas-sliders .sliders-bkg h2 {
  color: #FFFFFF;
  font-family: "Varela Round", sans-serif;
  font-weight: 600;
  visibility: hidden;
  transform: scale(1.5);
  transition: visibility 400ms;
  transition: transform 500ms;
  font-size: 2.4rem;
}
@media (min-width: 480px) {
  .cabezote .bkg-flechas-sliders .sliders-bkg h2 {
    font-size: 2.9rem;
  }
}
@media (min-width: 768px) {
  .cabezote .bkg-flechas-sliders .sliders-bkg h2 {
    font-size: 5rem;
  }
}
@media (min-width: 820px) {
  .cabezote .bkg-flechas-sliders .sliders-bkg h2 {
    font-size: 5.2rem;
  }
}
@media (min-width: 1200px) {
  .cabezote .bkg-flechas-sliders .sliders-bkg h2 {
    font-size: 6.6rem;
  }
}
.cabezote .bkg-flechas-sliders .sliders-bkg h2 span {
  padding: 0rem 1.3rem;
  text-transform: uppercase;
  background-color: rgb(211, 248, 0);
  color: rgb(0, 0, 0);
}

.cabezote {
  position: relative;
}

@media (min-width: 768px) {
  .contenido-secciones {
    position: absolute;
    left: 0;
    right: 0;
    bottom: -9%;
  }
}
@media (min-width: 768px) {
  .contenido-secciones .secciones-header {
    display: flex;
    justify-content: center;
  }
}
.contenido-secciones .secciones-header p {
  font-weight: 700;
  color: #a5a5a5;
  margin-top: 1rem;
  position: relative;
  transition: all 1.1s;
}
.contenido-secciones .secciones-header a {
  font-weight: 700;
  font-size: 1.4rem;
  color: #a5a5a5;
  margin-top: 1rem;
  position: relative;
  transition: all 1.1s;
}
.contenido-secciones .secciones-header h4 {
  color: rgb(10, 29, 85);
  position: relative;
  transition: all 1.1s;
}
.contenido-secciones .secciones-header .seccion-header {
  display: grid;
  grid-template-columns: 1fr 3fr;
  align-items: center;
  margin: 0 6rem;
  gap: 2rem;
  position: relative;
  background-color: #FFFFFF;
  text-align: center;
  padding: 1rem;
}
@media (min-width: 768px) {
  .contenido-secciones .secciones-header .seccion-header {
    width: 35rem;
    height: 15rem;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1.5rem;
    margin: 0;
    margin-right: 0.4rem;
    border-radius: 0.8rem;
  }
}
.contenido-secciones .secciones-header .seccion-header img {
  width: 9rem;
  position: relative;
}
.contenido-secciones .secciones-header .seccion-header:hover {
  cursor: pointer;
}
.contenido-secciones .secciones-header .seccion-header:hover p, .contenido-secciones .secciones-header .seccion-header:hover h4 {
  color: #0da6f3;
}
@media (min-width: 768px) {
  .contenido-secciones .secciones-header .seccion-header:hover p, .contenido-secciones .secciones-header .seccion-header:hover h4 {
    color: #FFFFFF;
  }
}
@media (min-width: 768px) {
  .contenido-secciones .secciones-header .seccion-header::before {
    content: "";
    width: 0;
    height: 0;
    background-color: #3d72fc;
    position: absolute;
    bottom: 0;
    left: 0;
    transition: all 0.6s ease;
    border-radius: 0.8rem;
  }
  .contenido-secciones .secciones-header .seccion-header:hover::before {
    cursor: pointer;
    width: 100%;
    height: 100%;
  }
}

.line-seudoelement::before {
  content: "";
  width: 6rem;
  height: 0.5rem;
  display: block;
  background-color: #0da6f3;
  margin: auto;
  margin-bottom: 3rem;
}

.informacion-sidebar {
  flex-basis: calc(20% - 1px);
  border: 1px solid #a5a5a5;
  background-color: #FFFFFF;
}
.informacion-sidebar hr {
  border-color: #FFFFFF;
}
.informacion-sidebar h3 {
  background-color: rgb(10, 29, 85);
  color: #e1e1e1;
  padding: 1rem;
}
.informacion-sidebar h4 {
  color: rgb(10, 29, 85);
  padding: 1rem;
}
.informacion-sidebar p {
  padding: 0 1rem;
  margin-bottom: 0.5rem;
  color: #585858;
}
.informacion-sidebar p:nth-of-type(5) {
  margin-bottom: 2rem;
}
.informacion-sidebar img {
  width: 5rem;
}
.informacion-sidebar .rq {
  font-weight: 600;
  margin-top: 1rem;
}

.carreras {
  background-color: rgb(10, 29, 85);
  padding-bottom: 4rem;
  padding-top: 6rem;
  box-shadow: 0 0 2rem rgba(10, 29, 85, 0.8);
}
.carreras h4 {
  text-align: center;
  color: #FFFFFF;
  margin-bottom: 5rem;
}
.carreras h4 span {
  font-size: 3rem;
  display: none;
}
.carreras h4 .maquina-escribir {
  display: inline-flex;
  white-space: nowrap;
  border-right: 4px solid #FFFFFF;
  width: 32.5rem;
  animation: escribir 4s steps(18), blinks 0.5s infinite step-end alternate;
  overflow: hidden;
}
@keyframes escribir {
  from {
    width: 0;
  }
}
@keyframes blinks {
  50% {
    border-color: transparent;
  }
}

.slider-container {
  padding: 1rem 0;
}
.slider-container .tarjetas {
  display: flex;
  align-items: center;
  width: 100%;
  margin: auto;
  overflow-x: hidden;
}
.slider-container .tarjetas::-webkit-scrollbar {
  display: none;
}
.slider-container .tarjetas .tarjeta {
  box-shadow: 3px 3px 11px rgba(207, 36, 36, 0.2);
  min-width: 100%;
  margin-right: 1.2rem;
  position: relative;
  border: 3rem solid rgb(10, 29, 85);
}
@media (min-width: 480px) {
  .slider-container .tarjetas .tarjeta {
    min-width: 50%;
    border: none;
  }
}
@media (min-width: 768px) {
  .slider-container .tarjetas .tarjeta {
    min-width: 33%;
  }
}
