  .slider-container .tarjetas .tarjeta {
    min-width: 19.13%;
  }
}
.slider-container .tarjetas .tarjeta .imagen {
  width: 100%;
  height: 40rem;
}
.slider-container .tarjetas .tarjeta .imagen img {
  width: 100%;
  height: 100%;
  border-radius: 0.8rem;
}
.slider-container .tarjetas .tarjeta .descrpcion-slider {
  width: 100%;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  text-align: center;
}
.slider-container .tarjetas .tarjeta .descrpcion-slider .slider_cursos {
  color: #F3F3F3;
  font-weight: 500;
  background-color: rgba(225, 225, 225, 0.2);
  padding: 1rem 2rem;
  border-radius: 0.8rem;
  border: 1px solid rgba(225, 225, 225, 0.4);
}
.slider-container .tarjetas .tarjeta .descrpcion-slider .slider_cursos:hover {
  background-color: rgba(225, 225, 225, 0.4);
  color: #FFFFFF;
  border: 1px solid rgba(225, 225, 225, 0.8);
}
.slider-container .tarjetas .tarjeta .descrpcion-slider h4 {
  margin-top: 3rem;
  font-size: 2rem;
}
.slider-container .tarjetas .tarjeta .descrpcion-slider h4 .Ningles {
  font-size: 2rem;
  display: inline;
  color: #0da6f3;
  font-weight: 900;
}
.slider-container .flechas-atras-adelante {
  margin-top: 3rem;
  display: flex;
  justify-content: space-around;
}
@media (min-width: 480px) {
  .slider-container .flechas-atras-adelante {
    justify-content: center;
    gap: 7rem;
  }
}
.slider-container .flechas-atras-adelante img {
  width: 4rem;
}
.slider-container .flechas-atras-adelante img:hover {
  cursor: pointer;
}

.tarjeta {
  overflow: hidden;
  transition: right 1500ms ease;
  right: 0rem;
}
.tarjeta:hover {
  cursor: pointer;
}
.tarjeta:hover img {
  transition: transform 1s ease;
  transform: scale(1.1);
}

