@use 'base/variables' as v;
@use 'base/mixins' as m;

.contacto-index {
    .line-seudoelement {
        h3 {
            text-transform: uppercase;
            font-size: 2.7rem;
            span {
                display: block;
            }
        }
    }
    margin-top: 3rem;
    
    h3 {
        color: v.$azuloscuro;
    }
}

.contacto-info{
    @media (min-width: 768px) {
        display: flex;        
    }

    .contacto-descripcion{
        h2 {
            font-size: 2.8rem;
        }
        h3 {
            font-weight: 400;
            font-size: 1.9rem;
        }
        h3, h2{   
            margin-bottom: 4rem;
            color: v.$blanco;
            span{
                color: v.$azul;
            }
        }
        a {
            color: v.$blanco;
        }
        img {
            width: 25rem;
            border-radius: 2rem;
            margin-top: 3rem;
        }
        h4{color: v.$azul;}
        color: v.$blanco;
        padding: 8rem 5rem;
        text-align: center;
        background-color: v.$azuloscuro;
        flex-basis: calc(50% - 1px);
        display: flex;
        flex-direction: column;
        justify-content: center; 
        align-items: center;   
    } 
    .contenido-formulario{
        flex-basis: calc(50% - 1px);
        background-image: url(../img/search_background.jpg);

        .contacto{
            h3{
                color: v.$azuloscuro;
            }
            
            background-color: rgba(255, 255, 255, 0.5);
            height: 100%;
            width: 100%;
            padding: 8rem 5rem;
            background-repeat: no-repeat;
            background-position: center center;
            background-size: cover;
           
            .formulario{
                input:not([type="submit"]){
                    display: block;
                    margin: 3rem auto;
                    width: 100%;
                    padding: 1.2rem;
                    border: none;
                    outline: none;
                    border-radius: .4rem;
                    &:focus{
                        border: 2px solid v.$azul;
                    }
                }
                textarea{
                    height: 11rem; 
                    width: 100%;
                    border-radius: .4rem;
                }
                input[type="submit"]{
                    border-radius: .4rem;
                    display: block;
                    margin: 3rem auto;
                    width: 100%;
                    padding: 1.2rem;
                    background-color: v.$azuloscuro;
                    color: v.$blanco;
                    border: none;
                    font-weight: 600;
                    &:hover{
                        cursor: pointer;
                    }
                }
            }
        }  
    }
}

