@use 'base/variables' as v;
@use 'base/mixins' as m;

#bachilleracademico{
    margin-top: 3rem;
    @include m.tablet{
        padding: 0 3rem;
    }
    
    .bachiller{
        @media (min-width: 960px){
            display: grid;
            grid-template-columns: 50% 50%;
            min-height: 40rem;
        }
        .img-bachilleres{
            width: 100%;
            position: relative;
            margin: 0 auto;
            @media (min-width: 960px){
                margin: 0;
            }
            @media (min-width: 480px){
                width: 32rem;
            }
            
            .img-bachiller1{
               width: 32rem;
               border-radius: 2rem;
            }
            .img-bachiller2{
                width: 28rem;
                position: absolute;
                top: 16rem;
                right: 0;
                border-radius: 2rem;
                @media (min-width: 480px){
                    left: 40%;
                }
                
            }
        }
        .texto-bachiller{
            
            margin-top: 18rem;
            @media (min-width: 960px){
                margin-top: 0;
                padding: 0 3rem;
            }
            ul{
                font-family: Varela Round,sans-serif;
                text-align: center;
                color: #585858;
                line-height: 1.7;
                margin: 2rem 0;
            }
            text-align: center;
            h2, h3{ color: v.$azuloscuro;
                    font-family: v.$fuente_principal;
                    margin-bottom: 1rem;
            }
            h4{
                color: v.$grisoscuro2;
                line-height: 1.6;
            }
            
            .bachiller-texto-contacto{display: flex;
               align-items: center;
               justify-content: center;
               gap: 1rem;
               margin-bottom: 2rem;
               color: v.$azul2;
            }
            p{
                font-family: v.$fuente_secundaria;
                margin-bottom: 2rem;
                color: v.$grisoscuro2;
                line-height: 2rem;
            }

        }
    }
}

.btn-bachiller{
    border-radius: 1rem;      
    display: inline-block;
    background-color: v.$azul2;
    color: v.$blanco;
    padding: 1.6rem 2rem;
    margin-bottom: 3rem;
    text-align: center; 
    @include m.tablet { 
        // display: flex;
        // flex-direction: column;
        // justify-content: center;
        // align-items: center;
        margin: 0 1rem;
        font-size: 1.3rem;
        margin-bottom: 2rem;
    }
    &:hover{
        cursor: pointer;
        background-color: v.$azul3;
    } 
}





////////////////////////pq estudiar con nosotros/////////////////
.contenido-destacados{
    //margin-bottom: 5rem;
    margin-top: 5rem;
    h2 {
        text-transform: uppercase;
    }
    h2, h4, h3{ 
        font-family: v.$fuente_principal;
        color: v.$azuloscuro;
        text-align: center;
    }

    .graduacion-cm{
        padding: 0 2rem;
        text-align: center;
        margin-bottom: 5rem;
        @include m.tablet{
            display: grid;
            grid-template-columns: 2fr 3fr 1fr;
            gap: 1rem;
        }
        @media (min-width: 960px){
            display: grid;
            grid-template-columns: 30% 44% 23%;
            gap: 2rem;
        }
      
        h4{
            margin-bottom: 1rem;
            margin-top: 2rem;
            @include m.tablet { 
                margin-top: 0;
             }
        }
        p{
            margin-top: 2rem;
            text-align: justify;
            line-height: 2rem;
            color: v.$grisoscuro2;
            font-size: 1.6rem;
            margin-bottom: 2rem;
            line-height: 1.6;
        }
        .matriculate{
            position: relative;
            padding: .5rem;
            border-radius: 1rem;
            border: 1px solid v.$gris;
            overflow: hidden;
            .contenido-matriculate{
                position: relative;
                background-color: v.$blanco;
                border-radius: 1rem;
                //height: 100%;
                p {
                    text-align: center;
                    margin-bottom: 4rem;
                }
                h4 {
                    font-size: 1.4rem;
                    padding-top: 1rem;
                    margin-top: 0;
                }
                @include m.tablet { 
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    h4 {
                        font-size: 1.4rem;
                        padding-top: 1rem;
                    }
                    p {
                        font-size: 1.3rem;
                        margin-bottom: 3rem;
                    }
                }
            }
            &::before{  //efecto borde animado
                content: "";
                border-radius: 1rem;
                //background-image: conic-gradient(v.$azul2 20deg, transparent 120deg);
                background-image: conic-gradient(transparent, transparent, transparent, transparent, v.$azul2, v.$azul2);
                //background: linear-gradient(v.$azul, v.$blanco);
                position: absolute;
                width: 200%;
                height: 350%;
                top: -125%;
                left: -50%;
                animation: rotar 4s linear infinite;
            }
            @keyframes rotar {
                0%{
                    transform: rotate(0deg);
                }
                100%{
                    transform: rotate(360deg);
                }
            }

        }//fin matriculate
        .img-graduacion {
            display: flex;
            flex-direction: column;
            justify-content: center;
        }
    }

    .descripcion-destacados{
        @media (min-width: 768px) {
            display: flex;
            //flex-direction: row-reverse;
        }
        margin-top: 4rem;
        .imagen-destacados{  //imagen grande
            flex-basis: calc(45% - 1px);
            //background-color: aqua;
            text-align: center;
            @include m.tablet { 
                img{
                    width: 37rem;
                    height: 100%;  
                }
             }
        }
        .descripcion{
            flex-basis: calc(55% - 1px);
            
            .destacado{
                margin: 0 auto;
                text-align: center;  // me centra la img y los textos
                margin-bottom: 3rem; //me separa cada beneficio 
                @media (min-width: 768px) {
                   display: flex;
                   gap: 2rem;
                }
                
                img{
                    /*
                    display: block; 
                    margin: 0 auto;  // para centrar imagen*/
                    width: 7rem;
                }
                .info-destacado{
                    text-align: left;
                    color: v.$grisoscuro2;
                    p{
                        font-size: 1.6rem;
                        text-align: justify;
                        line-height: 2;
                        padding: 0 2rem;
                    }
                    h4{ margin-bottom: 1rem;
                        text-transform: uppercase;
                        text-align: center;
                        @media (min-width: 768px){
                            text-align: left;
                            padding: 0 2rem;
                        }
                    }
                    
                }
            }
            
        }
    }
}


