@use 'base/variables' as v;
@use 'base/mixins' as m;

.line-seudoelement{
    &::before{  //creacion de seudo elemento, pinta linea horizontal verde antes del h2 ="cursos-populares" 
        content: ''; 
        width: 6rem;
        height: 0.5rem;
        display: block;
        background-color: v.$azul;
        margin: auto;
        margin-bottom: 3rem;
        
    }
}

.informacion-sidebar{
    flex-basis: calc(20% - 1px);
    border: 1px solid v.$grisoscuro;
    background-color: v.$blanco;
    hr{
        border-color: v.$blanco;
    }
    h3{
        background-color: v.$azuloscuro;
        color: v.$gris;
        padding: 1rem;
    }
    h4{
        color: v.$azuloscuro;
        padding: 1rem;
    }
    p{ padding: 0 1rem;
        margin-bottom: .5rem;
        color: v.$grisoscuro2;
        &:nth-of-type(5){
            margin-bottom: 2rem;
        }
    }
    img{width: 5rem;}

    .rq{
        font-weight: 600;
        margin-top: 1rem;
    }
}