@use 'base/variables' as v;
@use 'base/mixins' as m;

.cabezote {
    .nosotros {
        @include m.desktop { 
            min-height: 55rem;
         }
    }
}
.img-contacto {
    padding: 5rem 0;
    min-height: 40rem;
    @include m.desktop { 
        padding-top: 5rem;
        margin-top: 2rem;
    }

    &::before {
        content: '';
        background-color: v.$negro;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        opacity: .5;
    }
}

.header-contacto {
    background-image: url(../img/bannerBg.jpg); 
    filter: drop-shadow( 5px 5px 10px #e1e1e1 );
    position: absolute; 
    width: 100%;
    background-size: cover;
    background-position: right center;
    @include m.tablet { 
        background-position: none;
     }
     @include m.desktop { 
        margin-top: 0;
        background-position: center top;
      }
    
    .contacto-header {
        h2 {
            font-size: 3rem;
            font-family: v.$fuente_principal;
            font-weight: 600;
            position: absolute;
            left: 3rem;
            bottom: 0;
            color: v.$blanco;
            text-transform: uppercase;
            &::after {  //creacion de seudo elemento, pinta linea horizontal verde antes del h2 ="cursos-populares" 
                content: ''; 
                width: 13rem;
                height: 0.5rem;
                display: block;
                background-color: v.$azul;
                margin: auto;
                margin-bottom: 3rem;
                @include m.tablet { 
                    width: 15rem;
                }
                @include m.desktop { 
                    width: 22rem;
                }
            }
            @include m.tablet { 
                font-size: 4rem;
            }
            @include m.desktop { 
                font-size: 6rem;
            }
        }
    }
}

.bloque-info {
    @include m.tablet { 
        padding-top: 9rem;
     }
     @include m.tabletl { 
        padding-top: 4rem;
      }
    @include m.desktop { 
        padding-top: 2rem;
     }
}
.contenido-formulario1 {
    background-color: v.$gris;
    width: 100%;
    @include m.tabletl { 
        width: 50%;
        margin: 0 auto;
     }

    .contacto{
        h3{
            color: v.$azuloscuro;
            font-family: v.$fuente_principal;
            text-transform: uppercase;
            line-height: 1.4;
            font-size: 4rem;

            span {
                display: block;
                text-transform: none;
                font-size: 2rem;
            }
        }
        // background-color: rgba(255, 255, 255, 0.5);
        height: 67rem;
        width: auto;
        padding: 3rem 5rem;
        margin-top: 2rem;
        border-radius: 3rem;
        background-repeat: no-repeat;
        // background-position: center center;
        
        @include m.tablet { 
            padding: 2.5rem 5rem;
         }
        
         @include m.desktop { 
            height: 67rem;
          }
       
        .formulario{
            input:not([type="submit"]){
                display: block;
                margin: 3rem auto;
                width: 100%;
                padding: 1.2rem;
                border: none;
                outline: none;
                border-radius: .8rem;
                &:focus{
                    border: 2px solid v.$azul;
                }
            }
            input[type="submit"]{
                display: block;
                margin: 3rem auto;
                width: 100%;
                padding: 1.2rem;
                background-color: v.$azuloscuro;
                color: v.$blanco;
                border: none;
                border-radius: .8rem;
                font-weight: 600;
                &:hover{
                    cursor: pointer;
                }
            }
            textarea {
                height: 11rem; 
                width: 100%;
                border-radius: .8rem;
            }
                input[type="submit"]{
                    display: block;
                    margin: 3rem auto;
                    width: 100%;
                    padding: 1.2rem;
                    background-color: v.$azuloscuro;
                    color: v.$blanco;
                    border: none;
                    border-radius: .8rem;
                    font-weight: 600;
                    &:hover{
                        cursor: pointer;
                        background-color: v.$azul;
                    }
            }
        }
    }  
}

.bloque-direccion {
    display: block;
    padding: 3rem;
    @include m.tablet { 
        display: flex; 
        flex-direction: column;
        justify-content: center;
        width: 50%;
     }
     @include m.desktop {
        margin-right: 0;
      }
    
    .direccion {
        display: flex;
        flex-direction: column;
        justify-content: center;
        .icon-contacto {
            display: block;
            margin: 0 auto;
            img {
                max-width: 100%;
                height: auto;
                margin-bottom: 2rem;
            }
        }
        
        .info-direccion {
            h3 {
                color: v.$negro;
                margin-bottom: 1.4rem;
                font-size: 2rem;
            }
            text-align: center;
        }


        
        p {
            color: v.$negro;
            font-size: 1.6rem;
            margin-bottom: 1.2rem;
            white-space: pre-line;
        }
    }
}

