@use 'base/variables' as v;
@use 'base/mixins' as m;

.detallecurso{
    .nombrecurso{
        background-color: v.$azul2;
        padding: 2rem;
        //margin-top: 2rem;
        span{
            display: flex;
            align-items: center;
            justify-content: center;
            &::before, &::after{
                margin: 0rem 1rem;
                display: inline-block;
                content: "";
                width: 10rem;
                height: 2px;
                background-color: v.$blanco;
            }
            img{
                width: 5rem;
            }
        }
        h2{
            margin-top: 1rem;
            color: v.$blanco;
        }
    }
}

.detallecurso-contenido{
    .img-temario{
        @include m.tablet{
            display: flex;
        }
        .contenedor-img{
            flex-basis: calc(50% - 1px);
            padding: 1rem 3rem;
        }
        .detallecurso-img{
            border: 3px solid v.$azul2;
            border-radius: .8rem;
            padding: 1rem;
            height: 100%; 
            img{
                width: 100%;
                height: 100%; 
                border-radius: .8rem;
            }

        }
        .detallecurso-temario{
            flex-basis: calc(50% - 1px);
            padding: 1rem 3rem;
            .temario-contenido{
                border: 3px solid v.$azul2;
                border-radius: .8rem;
                height: 100%;
                padding: 1rem;
                border-bottom-right-radius: 15rem;
            }
            h3{
                color: v.$azul2;
            }
            h4{
                color: v.$grisoscuro2;
                line-height: 1.5;
                white-space: pre-line;
            }

        }
    }

    .tt-caracteristicas{
        color: v.$azul2;
        margin: 2rem 0 1rem 0;
    }

    .caracteristicas{
        border: 1px solid v.$gris;
        padding: 1rem;
        @include m.grid(2, 2rem);
        @include m.tablet{
            @include m.grid(4, 2rem);
        }
        .caracteristica{
            display: flex;
            flex-direction: column;
            align-items: center;
            img{
                width: 5rem;
            }
            h3, p{
                color: v.$grisoscuro2;
            }
        }
    }
    .descripcion{
        padding: 2rem;
        h3{
            margin: 2rem 0 1rem 0;  
            color: v.$azul2;
        }
        p{
            font-size: 1.8rem;
            line-height: 1.5;
        }
    }    

    .tt-curso{
        padding: 2rem;
        h3{
            color: v.$azul2;
            margin: 1rem;
        }
        p{
            color: v.$grisoscuro2;
            font-size: 1.8rem;
            text-align: center;
        }
    }
}

.detallecurso-info{
    margin: 4rem 0 2rem 0;
    .heading-info{
        margin-bottom: 2rem;
        color: v.$negro;
    }
    .text-info{
        text-align: center;
        color: v.$grisoscuro2;
    }
    .info-contenido{
        padding-top: 3rem;
        @include m.tablet{
            display: flex;
            justify-content: space-around;
        }
        .info{
            display: flex;
            flex-direction: column;
            align-items: center;
            h4{
                color: v.$grisoscuro2;    
            }
            p{
                margin: 1rem 0 2rem 0;
                color: v.$grisoscuro2;
            }
            &:nth-of-type(1){
                label{
                    a{
                        @include m.boton(v.$azul, v.$blanco);
                        margin: 0;
                        padding: 1rem 3rem;
                    }
                }
            }
            &:nth-of-type(2){
                label{
                    @include m.boton(v.$negro, v.$blanco);
                    margin: 0;
                    padding: 1rem 3rem;
                    width: auto;
                }
            }
            &:nth-of-type(3){
                label{
                   a{
                        @include m.boton(v.$verde, v.$blanco);
                        margin: 0;
                        padding: 1rem 3rem;
                   }
                }
            }
        }
    }
}