@use 'base/variables' as v;
@use 'base/mixins' as m;

.cursos{
    background-color: v.$grisclaro;
    padding: 3rem;
    h2{
        color: v.$grisoscuro2;
    }
    .cursos-a√±o{
        display: flex;
        justify-content: center;
        align-items: flex-end;
        color: v.$azul;
        font-weight: 600;
        &::before, &::after{
            margin: 0rem 1rem;
            display: inline-block;
            content: "";
            width: 10rem;
            height: 2px;
            background-color: v.$azul;
        }
    }
    .cursos-contenido{
        margin-top: 2rem;
        @include m.tablet{
            @include m.grid(2, 3rem);
        }
        @media (min-width: 1024px){
            @include m.grid(4, 3rem); 
        }
    }
    .curso{
        margin-bottom: 3rem;
        &:last-child{
            margin-bottom: 0rem;
        }
        @include m.tablet{
            margin: unset;
        }
        .curso-img{
            background-color: v.$blanco;
            border: 1px solid v.$gris;
            padding: .6rem .6rem .3rem .6rem;
            margin-bottom: 1rem;
            border-radius: .8rem;
            text-align: center;
        }
        .curso-texto{
            background-color: v.$blanco;
            border-radius: .8rem;
            overflow: hidden;
            h4{
                background-color: v.$azul2;
                text-align: center;
                padding: 1rem;
            }
            p{
                padding: 1.2rem 2rem;
                border: 1px solid v.$gris;
                text-align: center;
                color: v.$grisoscuro2;
            }
            .curso-btn{
                text-align: center;
                padding: 1.2rem 2rem;
                border: 1px solid v.$gris;
                a{
                    @include m.boton(v.$azul2, v.$blanco);
                    margin: 0;
                    display: inline;
                    padding: .8rem 2rem;
                    font-size: 1.4rem;
                    border-radius: .8rem;
                }
            }
        }
    }
}