@use 'base/variables' as v;
@use 'base/mixins' as m;

.btn-flotante{
    visibility: hidden;
    position: fixed;
    bottom: 2rem;
    right: 2rem;
    z-index: 2;
    background-color: v.$verde;
    border-radius: 50%;
    transition: all 1s ease;
    animation: ws 1.2s infinite;
    img{
        width: 6rem;
        
    }
    &:hover{
        transform: translate(-25%, -25%);
    }
}

@keyframes ws {
    0%{
        box-shadow: 0 0 0 0 rgba( v.$verde, 0.85);  //#05770a
    }
    100%{
        box-shadow: 0 0 0 25px rgba( v.$verde, 0.2);  //#07630b

    }
}