@use 'base/variables' as v;
@use 'base/mixins' as m;

.estudiantes{
    margin-top: 2rem;
    h1{text-align: left;
        color: v.$azuloscuro;
    }
}
.estudiantes-contenido{
    @include m.tablet{
    display: flex;
    gap: 1rem;
                     }

    

    .consulta-academico{
        flex-basis: calc(80% - 1px);
        .bg-consulta-academica{
            text-align: center;
            background-image: linear-gradient(to bottom right, v.$azuloscuro 25%, #1a318a 50%, #3d72fc 75%, #0da6f3 100%);
            background-size: 200% 200%;
            img{ width: 15rem;}
            animation: movimiento_fondo 4s linear infinite alternate;
        }

        @keyframes movimiento_fondo {
            from {
                background-position: 0 0;
            }
            to{
                background-position: 100% 100%;
            }
        }
        .texto-consulta{
            text-align: center;
            margin-bottom: 2rem;
            color: v.$azuloscuro;
        }
        form{
            @media (min-width: 768px){
                width: 45rem;
                margin: 0 auto;
            }
            
            .estudiantes-campo{
                input{
                    width: 100%;
                    padding: 1rem;
                    border: none;
                    outline: 1px solid v.$grisoscuro;
                    &:focus{
                      border: 2px solid v.$azul2;
                    }
                }
                @media (min-width: 768px){
                    display: flex;
                    align-items: center;
                    label{ flex: 0 0 19rem;}
                }
                margin-bottom: 2rem;
            }
            input[type="submit"]{  
                @media (min-width: 768px){
                    width: 26rem;
                    margin-left: 19rem;
                }
                color: v.$blanco;
                background-color: v.$azul2;
                &:hover{
                    cursor: pointer;
                    background-color: v.$azul3;
                }
            }
        }// fin form
        .result-consulta{
            border: 1px solid v.$gris;
            padding: 1rem 2rem;
           
            p{
                padding-top: 1rem;
                padding-left: 1rem;
                color: v.$grisoscuro2;
                font-size: 1.8rem;
                font-weight: 200;
                span{
                    color: v.$grisoscuro2;
                    font-size: 2rem;
                    font-weight: 900; 
                }
            }
            .datos-estudiantes{
                background-color: v.$gris;
                padding: 1rem;
                
                    color: v.$negro;
                    font-size: 1.6rem;
                    font-weight: 600;
                    span{
                        color: v.$negro;
                        font-size: 1.6rem;
                        font-weight: 400; 
                    }
                
            }
            
            
        }
    } //fin consulta-academico
}

.resultado-academico{
    border-bottom: 2px solid v.$gris;
    margin-bottom: 2rem;
}