@use 'base/variables' as v;
@use 'base/mixins' as m;

.container-oferta-academica{
    min-height: 50rem;  //altura que le da a la imagen de fondo
    background-image: url(../img/bg-ofertaacademica.jpg);  //bg-parallax4.jpg
    background-position: 73%;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    overflow: hidden;

    .barra-header, .info{
            position: relative;
            z-index: 1;
    }
    .contenido-portada-oferta-academica{
        position: absolute;
        background: linear-gradient(90deg, rgba(10,29,85,1) 16%, rgba(255,255,255,0) 85%);
        width: 100%;
        height: 100%;
        top: 8rem;
            
        h1, p{
        color: v.$blanco;
        text-align: center;
        }
        p{font-size: 1.6rem;}
        display: flex;
        flex-direction: column; 
        justify-content: center;        
    }
}  //header o portada superior


//main
.oferta-academica{ //a nivel de main
    h2{ color: v.$azuloscuro;
        margin-top: 2rem;
        text-align: left;
      }
    .contenido-programas-academicos{
        @media (min-width: 768px){
            display: flex;
            flex-direction: row-reverse;
            gap: 1rem;
        }       
        .programas{
            .programa{
                .programa-vermas{
                    padding: 0 1rem;
                    height: 0;
                    overflow: hidden;
                    position: relative;
                    color: v.$grisoscuro2;
                    .description{
                        text-align: center;
                        @include m.tablet{
                            text-align: left;
                        }
                    }
                    .contenido-programa{
                        .img-detalles{
                            display: flex;
                            flex-direction: column;
                           @media (min-width: 1000px) {
                                flex-direction: row; 
                            }
                            .img-programa{
                               @media (min-width: 1000px) {
                                    flex-basis: calc(55% - 1px); 
                                }    
                                img{
                                    border-top-left-radius: 8rem;
                                    border-bottom-right-radius: 8rem;
                                }
                            }
                            .detalles{
                               @media (min-width: 1000px) {
                                    flex-basis: calc(55% - 1px); 
                                    display: flex;
                                    flex-direction: column;
                                    justify-content: space-between;
                                }   
                                
                                ul{
                                    padding-left: 2rem;
                                    padding-top: 2rem;
                                    list-style-position: inside;
                                    text-align: center;
                                    @include m.telefono{
                                        display: grid;
                                        grid-template-columns: 50% 50%; 
                                        column-gap: 1rem;
                                        text-align: start;
                                    }
                                    li{
                                        font-weight: 600;
                                        color: v.$negro;
                                        i{
                                            color:#09EA00;
                                        }
                                        p{
                                            color: v.$grisoscuro2;
                                            font-weight: normal;
                                            text-align: center;  
                                            @include m.telefono{
                                                text-align: start;
                                            }
                                        }
                                    }
                                }
                                .OA{ //btn azul inscribete
                                    display: flex;
                                    justify-content: flex-end;
                                    a{margin: 0;}
                                }  
                            } //fin detalles
                        }//fin img-detalles
                        .resolucion-certificado{
                            margin-top: 2rem;
                            @media (min-width: 1000px) {
                                display: grid;
                                grid-template-columns: 49% 49%;
                                column-gap: 2rem;
                            }
                            h4{
                                color: v.$negro;
                                font-weight: 600;
                            }
                            .resolucion{
                                padding: 2rem;
                                box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
                            }
                            .certificado{
                                padding: 2rem;
                                box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
                            }
                            
                        }
                    }

                    &::after{
                        content: "";  //para generar linea horizontal inferior
                        position: absolute;
                        bottom: 0;
                        left: 0%;
                        width: 0%;
                        height: 4px;
                        //transition: all 0.2s ease;
                    }
                    transition: height .3s ease;    //efecto de la altura height
                }
            
                &:nth-of-type(2n+1){
                    .vermas-activo{  //clase que se agrega con js
                        height: 99rem;
                        @include m.telefono{
                            height: 95rem; 
                        }
                        @media (min-width: 1000px){
                            height: 53rem; 
                        }  
                        transition: height .5s ease;
                        
                        &::after{
                            content: "";
                            position: absolute;
                            bottom: 0;
                            left: 0%;
                            width: 100%;
                            height: 4px;
                            background-color: v.$azul;
                            transition: all 1s ease;
                        }
                    }
                }
                &:nth-of-type(2n){
                    .vermas-activo{  //clase que se agrega con js
                        height: 99rem;
                        @include m.telefono{
                            height: 95rem; 
                        }
                        @media (min-width: 1000px){
                            height: 53rem; 
                        }   
                        transition: height .5s ease;
                        
                        &::after{
                            content: "";
                            position: absolute;
                            bottom: 0;
                            left: 0%;
                            width: 100%;
                            height: 4px;
                            background-color: #8EE700;
                            transition: all 1s ease;
                        }
                    }
                }                
            } //fin programa
        } //fin programas
    }
} //fin main


.programa{
    
    p{ margin-bottom: 2rem;}

    h3, h4{font-weight: 500;}
    
    border-left: 5px solid v.$azul;  //pinta lineas azules y verdes a la izq
    margin-bottom: 1rem;
    &:nth-of-type(2n){
        border-left: 5px solid #8EE700;
        background-color: lighten(v.$gris, 8); //#efefef;
    }  
    .programa-detalle{
        h3, h4, p{text-align: center; }
        padding-top: 2rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        @include m.tablet{
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            padding-left: 1rem;
            padding-right: 1rem;
        }
        .nombre{ //div del nombre del programa
            @include m.tablet{
            width: 11rem;}
            @media (min-width: 960px){
                width: 32rem;
            }
            text-align: center;
            h4{ color: v.$grisoscuro2; }
            h3{color: v.$azuloscuro;
                display: inline-block;
                border: none;
                &.nombreh3{ //cuando clase este presenta por js 
                    border-bottom: 2px solid v.$azuloscuro;
                }
            }
            
        }
        .abierto{
            position: relative;
            color: v.$azul2;
            width: 9rem;
            margin: 0 auto;
            &::before{
                content: '';
                width: .8rem;
                height: .8rem;
                background-color: #09EA00;
                position: absolute;
                top: 20%;
                left: 0;
                border-radius: 50%;
                box-shadow: 0 0 0 3px rgba( #8EE700, 0.5);
            }
        }
        .v-mas{ 
            color: v.$grisoscuro2;
            font-weight: 600;
            position: relative;
            overflow: hidden;
            padding: .3rem;
            border-radius: 1rem;
            margin-bottom: 1rem;
            p{
                background-color: v.$blanco;
                position: relative;
                border: 1px solid v.$grisoscuro;
                margin: 0 auto;
                width: 10rem;
                position: relative;
                padding: 1rem;
                border-radius: 1rem;
                &::before{
                content: '+';
                position: absolute;
                left: 5%;
                }
                &:hover{
                    cursor: pointer;
                    border: none;
                }
            }
            .signo_pseudoelement{ //clase que se agrega en js
                &::before{ content: '-';}
            }
        }
        .v-mas::before{  //efecto borde animado
            content: '';
                width: 0;
                height: 0;
                background-color: v.$azul;
                position: absolute;
                bottom: 0;
                right: 0; 
                transition: all 0.5s ease;
                border-radius: .8rem; 
        }
        .v-mas:hover::before{ 
            width: 100%;
            height: 100%;
        }
    }
} //fin programa

